.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
  
    &:last-child {
      margin-right: 0;
    }
  }

  @for $n from 1 through 5 {
    &--#{$n} &__star:nth-child(-n + #{$n}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
